html {
  box-sizing: border-box;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}
* {
  margin: 0;
}
html {
  height: 100%;
}
body {
  overflow: hidden;
  background: -webkit-linear-gradient(
    top left,
    #007d35 0%,
    #007d35 40%,
    #0f574e 100%
  );
  min-height: 100%;
  background-position: center 0px, center center;
  background-repeat: no-repeat;
  background-size: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  body {
    align-items: center;
    background-size: 100vw;
  }
}

#root {
  width: 100%;
}
